body{
  display: flex;
  justify-content: center;

}
/* .App {
  text-align: center;
} */
nav{
  display: flex;
  position: relative;
  width: 1420px;
  height: 85px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px;
  background-color: #231d2b;
  border-bottom: 1px solid rgba(255, 255, 255, .2);
}
.link, .brand{
  text-decoration: none;
  font-family: 'League Spartan', sans-serif;
  padding: 10px;
  color: #F2F2F2;
  font-size: 16px;
}
.brand{
  position: absolute;
  width: 109px;
  height: 36px;
  left: 32px;
  top: 22px;
  font-family: 'Antonio', sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 36px;
  letter-spacing: -1.05px;}

.link:hover{
  cursor: pointer;
  background: #38384f;
}
.container{
  display: flex;
  flex-direction: column;
  width: 1440px;
  height: 938px;
  background-image: url(bg-stars.svg);
  background-color: #231d2b;

}
.description{
  width: 50%;
  height: 80%;
}
.earth1{
  width: 450px;
  height: 450px;
  margin-top: 20%;
  margin-left: 35%;
}
.parameter{
  width: 1440px;
  height: 20%;
  background-color: aqua;
}

